input {
  all: unset;
  appearance: none;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
}
body {
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}
button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}
form {
  width: 100%;
}
footer{
    text-align: center;
}
.nweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
}
/*Profile*/
.profileForm {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-bottom: 30px;
  border-bottom: 1px soild rgba(255, 255, 255, 0.9);
}
.logOut {
  margin-top: 50px;
}

/*Router*/
.router__Route {
  width: 100%;
  max-width: 890px;
  margin: 0 auto;
  margin: 80px;
  display: flex;
  justify-content: center;
}

/*Navigation*/
.navigation__ul {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  list-style: none;
}
.navigation__li_home {
  margin-right: 10px;
}
.navigation__li_profile {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
}
.navigation__span {
  margin-right: 10px;
  margin-top: 10px;
}
/*NweetFactory*/
.factoryForm {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.factoryInput__container {
  width: 100%;
  margin-bottom: 20px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  background-color: black;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}
.factoryInput__arrow {
  position: absolute;
  width: 40px;
  height: 40px;
  right: 0;
  color: white;
  background-color: #04aaff;
  padding: 10px 0px;
  text-align: center;
  border: none;
  border-radius: 20px;
}
.factoryInput__label {
  color: #04aaff;
  cursor: pointer;
}
.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}
.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.factoryForm__attachment img {
  width: 80px;
  height: 80px;
  border-radius: 40px;
}
.factoryForm__clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}
.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/*Nweet*/
.nweetEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}
.nweet {
  width: 100%;
  max-width: 320px;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
  background-color: white;
}
.nweet h4 {
  font-size: 14px;
}
.nweet img {
  width: 50px;
  height: 50px;
  top: 20px;
  right: -10px;
  margin-top: 10px;
  position: absolute;
  border-radius: 50%;
}
.nweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}
.nweet__actions span {
  cursor: pointer;
}
.nweet__actions span:first-child {
  margin-right: 10px;
}

/*Auth*/
.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}
.authBtn {
  width: 150px;
  font-size: 12px;
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 20px;
  padding: 10px 0px;
  background-color: white;
}

/*AuthForm*/
.authInput {
  width: 100%;
  max-width: 320px;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}
.authSubmit {
  text-align: center;
  background-color: #04aaff;
  color: white;
  margin-top: 10;
  cursor: pointer;
}
.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}
.authSwitch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}
